import React, { createContext, useEffect, useReducer } from 'react';
import authReducer, {
	AUTH_START,
	LOGIN_SUCCESS,
	LOGIN_FAIL,
	REGISTER_SUCCESS,
	REGISTER_FAIL,
	LOGOUT_SUCCESS,
	LOGOUT_FAIL,
	AUTH_CLEAN_UP,
} from './reducer';
import { loginUser, registerUser } from '../../services';
import { useAlert } from 'react-alert';
import { getToken, setToken } from '../../utils/helpers';

export const AuthContext = createContext();

const AuthProvider = (props) => {
	//with state allows you access anythig within the state
	//dispatch allows you to dispatch objects to the reducer
	const [state, dispatch] = useReducer(authReducer, {
		data: null,
		error: null,
		loading: true,
		currentUser: null,
		isAuthenticated: false,
		
	});
	
	useEffect(() => {
		try {
			dispatch({ type: AUTH_START });
			const token = getToken();
			console.log(token)

			if (!token) {
				throw Error('UnAuthenticated');
			}
			
			dispatch({
				type: LOGIN_SUCCESS,
				payload: JSON.parse(token),
			});
		} catch (error) {
			dispatch({
				type: LOGIN_FAIL,
				payload: error.message,
			});
		}
		// run once you load the app
	}, []);

	const login = async (values) => {
		try {
			dispatch({ type: AUTH_START });
			const res = await loginUser(values);
			const data = res.data.data.access_token;
			console.log(data);
			setToken(data);

			dispatch({
				type: LOGIN_SUCCESS,
				payload: res,
			});
		} catch (message) {
			dispatch({
				type: LOGIN_FAIL,
				payload: message,
			});
		}
	};

	const logout = () => {
		console.log('load user');
	};

	const cleanUp = () => {
		console.log('load user');
	};

	const register = async (values) => {
		try {
			dispatch({ type: AUTH_START });
			const res = await registerUser(values);

			dispatch({
				type: REGISTER_SUCCESS,
				payload: res.data,
			});
		} catch (error) {
			dispatch({
				type: REGISTER_FAIL,
				payload: error.res.message,
			});
		}
	};
	

	const { children } = props;
	return (
		<AuthContext.Provider
			value={{
				state,
				register,
				login,
				logout,
				cleanUp,
			}}
		>
			{children}
		</AuthContext.Provider>
	);
};

export default AuthProvider;
