import React, { createContext, useEffect, useReducer } from 'react';

import caseReducer, {
	DASH_START,
	GET_INITIAL_SUCCESS,
	GET_FINAL_SUCCESS,
	GET_EVALUATED_SUCCESS,
	GET_APPROVED_SUCCESS,
	GET_INITIAL_FAIL,
	GET_FINAL_FAIL,
	GET_EVALUATED_FAIL,
	GET_APPROVED_FAIL,
} from './reducer';

import {
	initialCasesList,
	finalCasesList,
	evaluatedCasesList,
	approvedCasesList,
} from '../../services';

export const DashboardContext = createContext();

const DashboardProvider = (props) => {
	//with state allows you access anythig within the state
	//dispatch allows you to dispatch objects to the reducer

	const [initialstate, dispatch] = useReducer(caseReducer, {
		initial: null,
		loading: false,
		final: null,
		error: null,
		evaluated: null,
		approved: null,
	});
    // getting cases with initial status
	const getinitial = async () => {
		try {
			dispatch({ type: DASH_START });
			const res = await finalCasesList();
			console.log(res);
			const data = res.data;
			dispatch({
				type: GET_INITIAL_SUCCESS,
				payload: data.data,
			});
		} catch (error) {
			console.log('new error', error);
			dispatch({
				type: GET_INITIAL_FAIL ,
				payload: error.message,
			});
		}
	};

    // getting caes with final status
    const getfinal = async () => {
		try {
			dispatch({ type: DASH_START });
			const res = await initialCasesList();
			console.log(res);
			const data = res.data;
			dispatch({
				type: GET_FINAL_SUCCESS,
				payload: data.data,
			});
		} catch (error) {
			console.log('new error', error);
			dispatch({
				type: GET_FINAL_FAIL,
				payload: error.message,
			});
		}
	};


    // getting caes with final status
    const getEvaluated = async () => {
		try {
			dispatch({ type: DASH_START });
			const res = await evaluatedCasesList();
			console.log(res);
			const data = res.data;
			dispatch({
				type: GET_EVALUATED_SUCCESS,
				payload: data.data,
			});
		} catch (error) {
			console.log('new error', error);
			dispatch({
				type: GET_EVALUATED_FAIL,
				payload: error.message,
			});
		}
	};
    // getting caes with final status
    const getApproved = async () => {
		try {
			dispatch({ type: DASH_START });
			const res = await approvedCasesList();
			console.log(res);
			const data = res.data;
			dispatch({
				type: GET_APPROVED_SUCCESS,
				payload: data.data,
			});
		} catch (error) {
			console.log('new error', error);
			dispatch({
				type: GET_APPROVED_FAIL,
				payload: error.message,
			});
		}
	};

    const { children } = props;
    console.log(initialstate);
    return (
      <DashboardContext.Provider
        value={{
			initialstate,
          getinitial,
        getfinal,
        getEvaluated,
        getApproved,

        }}
      >
        {children}
      </DashboardContext.Provider>
    );


}

export default DashboardProvider;
