export const GET_INITIAL_SUCCESS = "GET_INITIAL_SUCCESS";
export const GET_FINAL_SUCCESS = "GET_FINAL_SUCCESS";
export const GET_EVALUATED_SUCCESS = "GET_EVALUATED_SUCCESS";
export const GET_APPROVED_SUCCESS = "GET_APPROVED_SUCCESS";
export const GET_INITIAL_FAIL = "GET_INITIAL_FAIL";
export const GET_FINAL_FAIL = "GET_FINAL_FAIL";
export const GET_EVALUATED_FAIL = "GET_EVALUATED_FAIL";
export const GET_APPROVED_FAIL = "GET_APPROVED_FAIL";
export const DASH_START = "DASH_START";


const dashboardReducer = (state, action) => {
  switch (action.type) {
    case DASH_START:
      return {
        ...state,
        loading: true,
      };
    case GET_INITIAL_SUCCESS:
      return {
        ...state,
        loading: false,
        initial: action.payload,
        error: null,
      };
    case GET_INITIAL_FAIL:
      return {
        ...state,
        loading: false,
        initial: null,
        error: action.payload,
      };

    case GET_FINAL_SUCCESS:
      return {
        ...state,
        loading: false,
        error: null,
        final: action.payload,
      };

    case GET_FINAL_FAIL:
      return {
        ...state,
        loading: false,
        final: null,
        error: action.payload,
      };
    case GET_EVALUATED_SUCCESS:
      return {
        ...state,
        loading: false,
        error: null,
        evaluated: action.payload,
      };
    case GET_EVALUATED_FAIL:
      return {
        ...state,
        loading: false,
        evaluated: null,
        error: action.payload,
      };

    case GET_APPROVED_SUCCESS:
      return {
        ...state,
        loading: false,
        error: null,
        approved: action.payload,
      };
    case GET_APPROVED_FAIL:
      return {
        ...state,
        loading: false,
        appproved: null,
        error: null,
      };

    default:
      return state;
  }
};
export default dashboardReducer;
